@mixin clearfix($extend: true) {
    // ë¯¸ëì´ì¿¼ë¦¬ ë´ë¶ ì¬ì© ì $extend: false ì¬ì©
    @if $extend {
        @extend %clearfix;
    }
    @else {
        *zoom: 1;
        &:before, &:after {
            content: "";
            display: block;
        }
        &:after {
            clear: both;
        }
    }
}
%clearfix {
  @include clearfix($extend: false);
}