// Heading Retina Design Font Size ì¤ì 
$h2-fz: 26px;
$h3-fz: 23px;
$h4-fz: 20px;
$h5-fz: 18px;
$h6-fz: 16px;

h2, h3, h4 {
    font-weight: 500;
}
h5, h6 {
    font-weight: 400;
}

// ì ëª© í°í¸ ì¬ì´ì¦, íê° ë¹ì¨ (px, rem)
h2 {
    @include psd-fz($h2-fz);
    @include psd-lh($h2-fz);
}
h3 {
    @include psd-fz($h3-fz);
    @include psd-lh($h3-fz);
}
h4 {
    @include psd-fz($h4-fz);
    @include psd-lh($h4-fz);
}
h5 {
    @include psd-fz($h5-fz);
    @include psd-lh($h5-fz);
}
h6 {
    @include psd-fz($h6-fz);
    @include psd-lh($h6-fz);
}

pre, code {
    font-family: $font-family-ko;
    line-height: $base-line;
}